.gpt3__header {
  display: flex;
}

.gpt3__header-content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  margin-right: 5rem;
}

.gpt3__header-content h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 60px;
  line-height: 70px;
  letter-spacing: -4px;
}

.gpt3__header-content p {
  font-family: var(--font-family);
  font-size: 20px;
  font-style: normal;
  font-weight: normal;
  line-height: 27px;
  color: #81afdd;
  margin-top: 1.5rem;
}

.gpt3__header-content_input {
  width: 100%;
  margin: 2rem 0 1rem;
  display: flex;
}

.gpt3__header-content_input input {
  flex: 2;
  width: 100%;
  font-family: var(--font-family);
  font-size: 20px;
  line-height: 27px;
  background: var(--color-footer);
  border: 2px solid var(--color-footer);
  padding: 1.5rem 1rem;
  outline: none;
  color: #fff;
  border-radius: 5px 0 0 5px;
}

.gpt3__header-content_input button {
  color: #fff;
  padding: 1rem 1.5rem;
  background: #ff4820;
  font-size: 20px;
  font-weight: bold;
  font-family: var(--font-family);
  line-height: 27px;
  cursor: pointer;
  outline: none;
  border: none;
  border-radius: 0px 5px 5px 0px;
}

.gpt3__header-content_people {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 2rem;
}

.gpt3__header-content_people img {
  width: 180px;
  height: 40px;
}

.gpt3__header-content_people p {
  margin: 0 0 0 1rem;
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 12px;
  line-height: 38px;
  color: #fff;
}

.gpt3__header-image {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.gpt3__header-image img {
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 1050px) {
  .gpt3__header {
    flex-direction: column;
  }

  .gpt3__header-content {
    margin: 0 0 3rem;
  }
}

@media screen and (max-width: 650px) {
  .gpt3__header-content h1 {
    font-size: 48px;
    line-height: 60px;
  }

  .gpt3__header-content_people p {
    font-size: 16px;
    line-height: 24px;
    text-align: center;
  }

  .gpt3__header-content_people {
    flex-direction: column;
  }

  .gpt3__header-content_people p {
    margin: 1rem 0;
  }

  .gpt3__header-content_input input,
  .gpt3__header-content_input button {
    font-size: 16px;
    line-height: 27px;
    padding: 1rem ;
  }
  
}

@media screen and (max-width: 490px) {
  .gpt3__header-content h1 {
    font-size: 36px;
    line-height: 48px;
    letter-spacing: -2px;
  }

  .gpt3__header-content_people p {
    font-size: 16px;
    line-height: 24px;
    text-align: center;
  }
}
