.gpt3__cta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  padding: 3rem 2rem;
  margin: 4rem;
  border-radius: .5rem;
  background: var(--gradient-bar);
}

.gpt3__cta-content p {
  font-size: 14px;
  font-weight: 500;
  font-family: var(--font-family);
  line-height: 30px;
  color: #0e0e0e;
}

.gpt3__cta-content h3 {
  font-family: var(--font-family);
  font-size: 24px;
  font-weight: 800;
  line-height: 45px;
  text-transform: capitalize;
  color: #000000;
}

.gpt3__cta-btn button {
  border: none;
  background: #000000;
  color: #fff;
  font-family: var(--font-family);
  font-size: 18px;
  font-weight: 500;
  padding: 1rem 2rem;
  border-radius: 40px;
  cursor: pointer;
}

@media screen and (max-width: 990px) {
  .gpt3__cta {
    flex-direction: column;
  }

  .gpt3__cta-btn button {
    margin-top: 1rem;
  }
}
