.gpt3__features-container_feature {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-direction: row;
  margin: 1rem;
}

.gpt3__features-container_feature-title {
  flex: 1;
  max-width: 220px;
  margin-right: 2rem;
}

.gpt3__features-container_feature-title h1 {
  font-size: 20px;
  font-weight: 800;
  font-family: var(--font-family);
  line-height: 24px;
  color: #fff;
}

.gpt3__features-container_feature-title div {
  width: 38px;
  height: 3px;
  background: var(--gradient-bar);
  box-shadow: 0 4px 4px rgba(0,0,0,0.25);
  margin-bottom: 0.60rem;
}

.gpt3__features-container_feature-text {
  flex: 2;
  max-width: 500px;
  display: flex;
}

.gpt3__features-container_feature-text p {
  font-size: 14px;
  font-weight: 500;
  font-family: var(--font-family);
  line-height: 24px;
  color: #81afdd;
}

@media screen and (max-width: 550px) {
  .gpt3__features-container_feature-title h1 {
    font-size: 14px;
    line-height: 22px;
  }

  .gpt3__features-container_feature-text p {
    font-size: 12px;
    line-height: 20px;
  }

  .gpt3__features-container_feature {
    margin: 1rem 0;
  }
}