.gpt3__footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: var(--color-footer);
}

.gpt3__footer-heading {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  width: 100%;
  margin-bottom: 3rem;
  margin-top: 4rem;
}

.gpt3__footer-heading h1 {
  font-size: 55px;
  font-weight: 800;
  font-family: var(--font-family);
  line-height: 75px;
  letter-spacing: -2px;
  max-width: 800px;
}

.gpt3__footer-btn p {
  font-size: 18px;
  font-family: var(--font-family);
  line-height: 20px;
  color: var(--color-text);
  border: 1px solid var(--color-text);
  padding: 1rem;
  cursor: pointer;
  margin-bottom: 10rem;
}

.gpt3__footer-links {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  width: 100%;
  text-align: left;
}

.gpt3__footer-links div {
  width: 250px;
  margin: 1rem;
}

.gpt3__footer-links_logo {
  display: flex;
  flex-direction: column;
}

.gpt3__footer-links_logo img {
  width: 120px;
  height: 30px;
  margin-bottom: 2rem;
  }

  .gpt3__footer-links_logo p {
    font-size: 14px;
    line-height: 18px;
    font-family: var(--font-family);
    color: #ffffff;
  }

  .gpt3__footer-links_div {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
  }

  .gpt3__footer-links_div h4 {
    font-size: 20px;
    font-family: var(--font-family);
    line-height: 22px;
    color: var(--color-text);
    margin-bottom: 1rem;
  }

  .gpt3__footer-links_div p {
    font-size: 16px;
    font-family: var(--font-family);
    color: #fff;
    line-height: 20px;
    margin: 1rem 0;
    cursor: pointer;
    font-weight: 600;
  }

  .gpt3__footer-copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;
  }

  .gpt3__footer-copyright p {
    font-family: var(--font-family);
    font-size: 14px;
    line-height: 16px;
    color: #fff;
  }

  @media screen and (max-width: 850px) {
    .gpt3__footer-heading h1 {
      font-size: 40px;
      line-height: 45px;
    }
  }

  @media screen and (max-width: 550px) {
    .gpt3__footer-heading h1 {
      font-size: 30px;
      line-height: 38px;
    }

    .gpt3__footer-links div {
      margin: 1rem 0;
    }

    .gpt3__footer-btn p {
      font-size: 14px;
      line-height: 20px;
    }
  }

  @media screen and (max-width: 390px) {
    .gpt3__footer-heading h1 {
      font-size: 22px;
      line-height: 32px;
    }
    
    .gpt3__footer-btn p {
      font-size: 12px;
      line-height: 20px;
      padding: .5rem 1rem;
      margin-bottom: 5rem;
    }

    .gpt3__footer-links_div p {
      font-size: 12px;
    }
  }